    // },
    shareToWeixin: function (scene, story) {
      if (!story) {
        uni.chooseImage({
          count: 1,
          sizeType: ['compressed'],
          sourceType: ['album'],
          success: (res) => {
            uni.share({
              provider: 'weixin',
              type: 2,
              title: '点书吧',
              scene: scene,
              imageUrl: res.tempFilePaths[0],
              success: (res) => {
                uni.showToast({
                  title: '分享成功！'
                })
              }
            })
          }
        })
      } else {
        uni.share({
          provider: 'weixin',
          type: 0,
          title: '点书吧',
          scene: scene,
          imageUrl: story.pic,
          href: story.url,
          summary: story.content,
          success: (res) => {
            uni.showToast({
              title: '分享成功！'
            })
          }
        })
      }
    },
    close: function () {
      this.$emit('closeSharePop', false)
    }
  }
}
